$(document).ready(function(){
    $('#first_name').keyup(function() {
    $('#name'). val($("#first_name") . val()  + "  " + $('#last_name').val());
     });

     $('#last_name').keyup(function() {
     $('#name'). val($("#first_name") . val()  + "  " + $(this).val());
    });

        $("body") .click(function(){
        $('#name'). val($("#first_name") . val()  + "  " + $('#last_name').val());
    });
});

